import { Injectable } from "@angular/core";
import { BehaviorSubject, Subject } from "rxjs";
@Injectable({
  providedIn: "root",
})
export class orderInvoiceReport {
  public getPdfContent = new BehaviorSubject("");


  numberToWords(num: number): string {
    const ones = ['', 'One', 'Two', 'Three', 'Four', 'Five', 'Six', 'Seven', 'Eight', 'Nine'];
    const teens = ['Ten', 'Eleven', 'Twelve', 'Thirteen', 'Fourteen', 'Fifteen', 'Sixteen', 'Seventeen', 'Eighteen', 'Nineteen'];
    const tens = ['', '', 'Twenty', 'Thirty', 'Forty', 'Fifty', 'Sixty', 'Seventy', 'Eighty', 'Ninety'];
    if (num === 0) return 'Zero';
    let words = '';
    if (num >= 1000) {
      words += this.numberToWords(Math.floor(num / 1000)) + ' Thousand ';
      num %= 1000;
    }
    if (num >= 100) {
      words += ones[Math.floor(num / 100)] + ' Hundred ';
      num %= 100;
    }
    if (num >= 20) {
      words += tens[Math.floor(num / 10)];
      num %= 10;
      if (num > 0) words += ' ';
    }
    if (num >= 10) {
      words += teens[num - 10];
      num = 0;
    }
    if (num > 0) {
      words += ones[num];
    }
    return words.trim();
  }

  generatePdfContent(getDetail) {
    let totalQuantity = 0;
    let totalAmount = 0;
    const makePdf = `<div class="card invoice-preview-card">
    <div class="card-body invoice-padding pb-0">
      <div
        class="d-flex justify-content-between flex-md-row flex-column invoice-spacing mt-0"
      >
        <div>
          <div class="logo-wrapper mb-0">
            <img src="../../../../assets/images/logo/logo.svg" />
          </div>
          <p class="card-text mb-25">
            Plot No. 329/C Commercial Area Tariq Road,PECHS Block 2
            Karachi
          </p>
          <p class="card-text mb-0">+92 333 3346598</p>
          <p class="card-text mb-0">Info@needlework.pk</p>
          <p class="card-text mb-0">www.needlework.pk</p>
        </div>
        <div class="mt-md-0 mt-2">
          <h4 class="invoice-title mt-1">
            Order Number
            <span class="invoice-number">#NW-00${getDetail.id}</span>
          </h4>
          <div class="invoice-date-wrapper">
            <p class="invoice-date">Order Date:</p>
            <p class="invoice-date-title">${getDetail.order_date}</p>
          </div>
          <div class="invoice-date-wrapper">
            <p class="invoice-date">Delivery Date:</p>
            <p class="invoice-date-title">${getDetail.actual_date}</p>
          </div>
          <div class="invoice-date-wrapper">
            <p class="invoice-date">Item Name:</p>
            <p class="invoice-date-title">${getDetail.item_master}</p>
          </div>
        </div>
      </div>
    </div>
    <hr class="m-0" />
    <div class="card-body invoice-padding pt-0">
      <div class="row invoice-spacing">
        <div class="col-6 p-0">
          <table>
            <tbody>
              <tr>
                <td class="pr-1">Customer Name:</td>
                <td>${getDetail.customer_name}</td>
              </tr>
              <tr>
                <td class="pr-1">Person Name:</td>
                <td>${getDetail.person_name}</td>
              </tr>
              <tr>
                <td class="pr-1">Address:</td>
                <td>${getDetail.address}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="col-6 p-0">
          <div class="float-right">
            <table>
              <tbody>
                <tr>
                  <td class="pr-1">Mobile No:</td>
                  <td>${getDetail.customer_phone}</td>
                </tr>
                <tr>
                  <td class="pr-1">City:</td>
                  <td>${getDetail.city}</td>
                </tr>
                <tr>
                  <td class="pr-1">Email:</td>
                  <td>${getDetail.email}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <div class="table-responsive">
      <table class="table">
        <colgroup>
        <col width="10%" />
        <col width="20%" />
        <col width="10%" />
        <col width="10%" />
        <col width="20%" />
        <col width="30%" />
       </colgroup>
        <thead>
          <tr>
            <th class="py-1">Image</th>
            <th class="py-1">Stich Type</th>
            <th class="py-1">Qty</th>
            <th class="py-1">Price</th>
            <th class="py-1">Amount</th>
            <th class="py-1">Note</th>
          </tr>
        </thead>
        <tbody>
        ${getDetail.details.map(
          (element) =>{
            totalQuantity += element.quantity
            totalAmount += element.amount
             return `<tr class="border-bottom">
              <td class="py-0 px-25"><img src="assets/images/order/${element.imageName}" height="70px" width="100px"/></td>
              <td class="py-1">${element.stich_type}</td>
              <td class="py-1">${element.quantity}</td>
              <td class="py-1">${element.price}</td>
              <td class="py-1">${element.amount}</td>
              <td class="py-1">${element.note}</td>
            </tr>`
          }
        )}
          <tr class="border-bottom border-top bg-light">
            <td colspan="2">
              <span class="font-weight-bold">Total</span>
            </td>
            <td colspan="2"> <span class="font-weight-bold">${totalQuantity}</span></td>
            <td colspan="2"> <span class="font-weight-bold">${totalAmount}</span></td>
          </tr>
          <tr class="border-bottom border-top ">
            <td colspan="2">
              <span class="font-weight-bold">Amount in Words:</span>
            </td>
            <td colspan="4"> <span class="font-weight-bold">${this.numberToWords(totalAmount)}</span></td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="card-body invoice-padding pt-0">
      <div class="row">
        <div class="col-12" style="height: 100px">
          <span class="font-weight-bold">Note: </span>
          <span
            >${getDetail.note}</span
          >
        </div>
      </div>
    </div>
  </div>`;
    this.getPdfContent.next(makePdf);
  }

  getData() {
    return this.getPdfContent.asObservable();
  }
}
