import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import { Observable } from "rxjs";
import { tap } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class ReportService {
  private baseUrl = environment.apiUrl;
  private path = "report";
  constructor(private _http: HttpClient) {}

  orderInvoiceReport(orderId): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/invoiceReport`, orderId)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  
  customerLedgerReport(customerData): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/ledgerReport`, customerData)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }
    
}
